.chatListItem {
  display: flex;
  cursor: pointer;
  align-items: center;
  height: 70px;

  &:hover {
    background-color: #f5f5f5;
  }
}

.chatListItem.active {
  background-color: #ebebeb;
}

.chatListItem--avatar {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  margin-left: 15px;
}
.chatListItem--lines {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-bottom: 1px solid #eee;
  padding-right: 15px;
  margin-left: 15px;

  flex-wrap: wrap;
  min-width: 0px;
}
.chatListItem--line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.chatListItem--name {
  font-size: 17px;
  color: black;
}

.chatListItem--date {
  font-size: 12px;
  color: #999;
}

.chatListItem--lastMsg {
  font-size: 14px;
  color: #999;
  display: flex;
  width: 100%;
  p {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0;
  }
}
